import React, {Component} from 'react';
import * as RecordAPI from '../utils/RecordsAPI';
import DatePicker from 'react-datepicker';
import moment from 'moment';

import 'react-datepicker/dist/react-datepicker.css';



class RecordForm extends Component {
    constructor(props) {
        super(props);
        this.state = {
            date: null,
            title: "",
            amount: "",
        };
        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
        this.handleDateChange = this.handleDateChange.bind(this);
    }

    valid() {
        return this.state.date && this.state.title && this.state.amount;
    }

    handleChange(event) {
        const {name, value} = event.target;
        this.setState({
            [name]: value
        });
    }

    handleDateChange(date) {
        this.setState({
            date: date
        });
    }

    handleSubmit(event) {
        event.preventDefault();
        const data = {
            date: this.state.date,
            title: this.state.title,
            amount: Number.parseInt(this.state.amount, 0)
        };
        RecordAPI.create(data).then(
            response => {
                this.props.handleNewRecord(response.data.data);
                this.setState({
                    date: null,
                    title: "",
                    amount: ""
                });
            }
        ).catch(
            error => console.log(error.message)
        )
    }

    render() {
        const placeholderText = "Date";
        return (
            <form className="form-inline mb-3" onSubmit={this.handleSubmit}>
                <div className="form-group mr-1">
                    <DatePicker
                        className="form-control"
                        selected={this.state.date}
                        onChange={this.handleDateChange}
                        dateFormatCalendar={"MMM YYYY"}
                        minDate={moment().subtract(6, "month")}
                        maxDate={moment().add(6, "month")}
                        showMonthYearDropdown
                        placeholderText={placeholderText}
                        isClearable={true}
                        dateFormat="YYYY/MM/DD"
                    />
                </div>
                <div className="form-group mr-1">
                    <input type="text" className="form-control" onChange={this.handleChange} placeholder="Title" name="title" value={this.state.title} />
                </div>
                <div className="form-group mr-1">
                    <input type="text" className="form-control" onChange={this.handleChange} placeholder="Amount" name="amount" value={this.state.amount} />
                </div>
                <button type="submit" className="btn btn-primary" disabled={!this.valid()}>Create Record</button>
            </form>
        );
    }
}

export default RecordForm;


